body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: flex-start;
  color: white;
  margin-left: 1px;
  align-items: center;
  background-image: url(/rdm-portfolio/static/media/space-bg.c2dc8aab.jpg); 
  background-attachment:scroll;
  background-size:cover;
  background-repeat:no-repeat;
}

a {
  text-decoration: none;
}

#navopen {
  position: fixed; 
  z-index: 1; 
  top: 0; 
  left: 2.5rem;
}

.navBtnContainer {
  margin: 1.5rem;
  background-color: white;
  border-width: 3px;
  border-style: solid;
  border-radius: 10%;
  border-color: black;
  min-width: 100px;   
  padding-top: 0.5rem; 
}

.navclose {
  margin: 1.5rem;
  background-color: white;
  border-width: 3px;
  border-style: solid;
  border-radius: 10%;
  border-color: black;
  min-width: 100px;   
}

#navopen:hover {
  cursor: pointer;
}

.navBtnContainer:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5px black;
}

.navclose:hover {
  cursor: pointer;
  box-shadow: inset 0 0 5px black;
}

.navBtn {
  margin: 0;
  padding: 0.25rem 0.5rem;
  color: black;
}

.theX {
  margin: 0;
  padding: 0;
  color: black;
}

.sidenav {
  height: 100%;  
  width: 25%;  
  position: fixed; 
  z-index: 1; 
  top: 0; 
  left: 0; 
  background-image: url(/rdm-portfolio/static/media/card-bg.420e92e4.jpg);  
  transition: .5s ease-out; 
  overflow-x: hidden; 
  padding-top: 20px; 
  display: flex; 
  flex-direction: column; 
  align-items: center;
}

.header {
  width: 80%;
  background-color: black;
  display: flex;
}

#theSelf {
  margin-left: 50px;
  margin-top: 50px;
  box-shadow: 2px 4px 8px 2px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 100%;
  min-width: 200px;
  max-width: 400px;
  -ms-flex-align: start;
}

.myNames {
  align-self: center;
  margin-left: 2rem;
  padding-right: 2rem;
}

#rauoldmoore {
  font-family: 'Cinzel', 'Righteous';
}

#rudieg {
  font-family: 'Cookie', 'Righteous';
  font-size: larger;
}

.skillsBorderBox {
  display: flex;
  padding: 1rem;
  position: relative;
  padding: 3px;
  margin-top: 10rem;
  margin-left: 5rem;
  align-items: center;
  flex-direction: column;
  background: black;
}

#skillsContainer {
  padding-left: 15rem;
  padding-right: 15rem;
  height: 7rem;
  background: black;
  text-align: center;
  overflow: hidden;
  width: 40%;
}

#skill {
  font-family: 'Staatliches';
  background:black;
  font-size: 22px;
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  line-height: 50px;
  text-align: center;
  /* Starting position */
  -webkit-transform:translateY(-100%);	
  transform:translateY(-100%);
  /* Apply animation to this element */
  -webkit-animation: animateskills 5s linear infinite;
  animation: animateskills 5s linear infinite;
}
/* Move it (define the animation) */
@-webkit-keyframes animateskills {
  0%   { -webkit-transform: translateY(-100%); }
  100% { -webkit-transform: translateY(100%); }
}
@keyframes animateskills {
  0%   { /* Firefox bug fix */
  -webkit-transform: translateY(-100%); /* Firefox bug fix */
  transform: translateY(-100%); 		
  }
  100% { /* Firefox bug fix */
  -webkit-transform: translateY(100%); /* Firefox bug fix */
  transform: translateY(100%); 
  }
}

.about {
  width: 80%;
  height: 40rem;
  padding-top: 2.5rem;
  display: flex; 
  flex-direction: column; 
  align-content: center;
  background-color: black;
}

.aboutContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.aboutCard {
  padding: 2%;
  flex: 0.5 0 400px;
  flex-wrap: wrap;
  border-width: 0.5px 1px;
  background-image: url(/rdm-portfolio/static/media/flower-card-bg.8e3697d0.jpg);
  background-repeat: no-repeat;
  background-position: 5px center;
  height: 100%;
  color: black;
}

.aboutCard h4 {
  color:white;
}

.aboutHead {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
  margin-left: 1rem;
  text-align: center;
  width: 55%;
  background: rgb(37, 37, 37);
  box-shadow: inset 0 0 5px black;
  flex-wrap: wrap;
}

.aboutHead h3 {
  text-decoration: underline;
  font-family: 'Righteous';
}

.aboutHead p {
  margin-right: 2rem;
}

.aboutHead span {
  font-family: 'Staatliches';
  color: lightblue;
  font-weight: lighter;
}

.aboutHead p {
  text-align: left;
  margin-left: 3rem;
  margin-bottom: 2rem;
  width:87%;
}

.aboutCard li {
  list-style-type: none;
  margin: 1.5rem;
  background-color: white;
  border-style: groove;
  border-color: black;
  border-radius: 15%;
  min-width: 100px;   
  padding: 1rem;
}

h3 {
  color:  white;
}

.nativenav {
  display: flex;
  flex-direction: row;
  margin: 2rem;
}

.nativenav h4 {
  color: white;
}

.nativenav .navBtnContainer {
  padding: 0;
}

#projectListContainer {
  margin-top: 4rem;
}

#projectDisplay {
  align-self: center;
  margin: 0 10%;
  align-content: center;
}

.projectListItem {
  border: solid 1px white;
  border-radius: 5%;
  background: black;
  padding: 0.1rem;
  margin: 0 2rem;
}

.projectListItem:hover {
  box-shadow: inset 0 0 10px white;
}

.projectListItem:active {
  background-color: white;
}
.projectCard {
  display: flex;
  flex-direction: column;
  align-content: center;
  padding: 15px 0px;
  flex: 0.5 0 400px;
  flex-wrap: wrap;
  background: rgb(37, 37, 37);
  box-shadow: inset 0 0 5px black;
  width: 85%;
  text-align: center;
  margin-left: 7rem;
  color: white;
}

.projectDetails {
  color: lightblue;
  font-family: 'Roboto', sans-serif;
  font-weight: 1500;
}

.projectCard a {
  color:lightpink;
  text-decoration: none;
  margin: 1rem 0;
}

.projectCard img {
  width:80%;
}

.contact {
  padding-top: 2.5rem;
  display: flex; 
  flex-direction: column; 
  align-items: center;
  background-color: black;
}

.contactForm   {
  background-image: url(/rdm-portfolio/static/media/card-bg.420e92e4.jpg);
  width: 100%;
  height: 100%;
}

.GoodNeighbor {
  font-family: 'Pacifico', cursive;
  color: white;
  font-weight: 1500;
}

.BootCampBox {
  color: white;
  font-family: 'Righteous';
  font-weight: 1500;
}

.Ebuy {
  color: white;
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-weight: 1500;
}

h2 {
  font-family: 'Staatliches', 'Righteous';
  text-decoration: underline;
}

.projectList {
  margin: 2rem;
  padding: 5rem;
  display: inline-flex;
  flex-direction: row;
}

.projectListItem {
  display:inline;
}


.projectList span {
  margin: 1rem 5rem;
}

.footer {
  bottom: 2.5rem;
  width: 95%;
  padding: 3rem;
  margin-top: 5rem;;
  background: rgb(37, 37, 37);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: smaller;
}

@media screen and (max-width:1900px) {
  .header {
    flex-direction: column;
  }
  .skillsContainer {
    align-self: center;
  }
}

@media screen and (max-width:992px) {
  .App {
    background: black;
  }
  .aboutHead {
    width:120%;
  }
  .projectCard {
    margin-left:2rem;
  }
  .projectList {
    display: flex;
    flex-direction: row;
  }
  .projectList span {
    margin: 20px;
  }
}

@media screen and (max-width:1500px) {
  .aboutHead {
    width: 95%;
  }
}

@media screen and (max-width:1500px){
  .header {
    flex-direction: column;
  }
  .skillsContainer {
    align-self: center;
  }
  .aboutContainer {
    flex-direction: column;
    margin-bottom: 2rem;
  }
  .about {
    width: 60%;
    height: 10%;
  }
  .aboutCard {
    width: 100%;
  }
  .aboutCard li {
    width: 80%;
  }
  .projectListContainer {
    display: flex;
    flex-direction: row;
    align-content: center;
  }
  .projectList {
    width: 80%;
  }
}
